#selector {
  position: fixed;
  height: 400px;
  width: 30px;
  right: 20px;
  top: 45vh;
  z-index: 100;
  opacity: 0.8;
  transform: scale(1.5);
}

.grow {
  transform: scale(1.1);
  background-color: #023E8A !important;
}

#tray {
  position: absolute;
  width: 2px;
  background-color: #c5c5c5;
  left: 14px;
  height: 200px;
}

.circle-container {
  height: 30px;
  width: 30px;
  position: relative;
}

.circle {
  height: 20px;
  width: 20px;
  background-color: #0077B6;
  border-radius: 50%;
  left: 5px;
  top: 5px;
  transition: transform 0.5s, border 0.5s, background-color 0.5s;
  position: absolute;
  border: 1px solid transparent;
}

.circle:hover {
  transform: scale(1.1);
  background-color: #023E8A;
}

